#buttons {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

#new-quote, #tweet-quote {
  padding: 12px 20px;
  border: none;
  color: white;
  font-size: 1em;
  border-radius: 4px;
  -webkit-transition: background-color 2s;
  -o-transition: background-color 2s;
  transition: background-color 2s;
}

#new-quote:hover,
#tweet-quote:hover {
  opacity: 0.8;
}

#new-quote:focus {
  outline: 0;
}


